import React from 'react';
import ReactDOM from 'react-dom/client';
import App from './App';

const rootElement = document.getElementById('root');
if (!rootElement) {
  console.error("Root element with id 'root' not found!");
}
const root = ReactDOM.createRoot(rootElement);

// Render the main app
root.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>
);
